.d-error {
  display: flex;
  align-items: center;
  height: 80%;
  max-width: 760px;
  min-height: 500px;
  margin: 0 auto;
}

.d-error-icon {
  flex: 0 0 62.5%;
  width: 62.5%;
  zoom: 1;
  max-width: 430px;
  height: 360px;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;
}

.d-error-404-icon {
  &:extend(.d-error-icon);
  background-image: url("http://docs.antjob.ink/funenc/question.svg");
}

.d-error-500-icon {
  &:extend(.d-error-icon);
  background-image: url("http://docs.antjob.ink/funenc/danger.svg");
}

.d-error-403-icon {
  &:extend(.d-error-icon);
  background-image: url("http://docs.antjob.ink/funenc/lock.svg");
}

.d-error-content {
  flex: auto;
  padding-left: 30px;
}

.d-error-code {
  font-weight: 600;
  font-size: 72px;
  line-height: 72px;
  color: #858e97;
}

.d-error-desc {
  font-size: 1rem;
  color: rgba(0, 0, 0, .45);
}

.d-opacity-loop {
  animation: opacityLoop 3s infinite;
  transition: .5s;
}
  
@keyframes opacityLoop
{
  0% {
    opacity: .6;
  }
  50% {
    opacity: .8;
  }
  100% {
    opacity: .6;
  }
}
@primary-color: #1890ff;